import { useState, useCallback, useRef, useEffect } from 'react';
import { mealAnalysisApi } from '../../lib/api';
import { useToast } from '../ui/Toast';
import type { FoodItem, NutritionTotals, FoodItemChange, ChatMessage } from '@lifestyle-app/shared';

interface MealChatProps {
  mealId: string;
  currentFoodItems: FoodItem[];
  onUpdate: (foodItems: FoodItem[], totals: NutritionTotals) => void;
}

interface DisplayMessage {
  id: string;
  role: 'user' | 'assistant';
  content: string;
  changes?: FoodItemChange[];
  isStreaming?: boolean;
}

export function MealChat({ mealId, currentFoodItems: _currentFoodItems, onUpdate }: MealChatProps) {
  const toast = useToast();
  const [messages, setMessages] = useState<DisplayMessage[]>([]);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [pendingChanges, setPendingChanges] = useState<FoodItemChange[]>([]);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Load chat history on mount
  useEffect(() => {
    const loadHistory = async () => {
      try {
        const { messages: history } = await mealAnalysisApi.getChatHistory(mealId);
        setMessages(
          history.map((msg: ChatMessage) => ({
            id: msg.id,
            role: msg.role,
            content: msg.content,
            changes: msg.appliedChanges,
          }))
        );
      } catch (error) {
        console.error('Failed to load chat history:', error);
        // Show error but don't block chat functionality
        if (error instanceof TypeError && (error as TypeError).message === 'Failed to fetch') {
          toast.warning('„ÉÅ„É£„ÉÉ„ÉàÂ±•Ê≠¥„ÅÆË™≠„ÅøËæº„Åø„Å´Â§±Êïó„Åó„Åæ„Åó„Åü');
        }
      }
    };
    loadHistory();
  }, [mealId, toast]);

  // Auto-scroll to bottom
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const handleSend = useCallback(async () => {
    if (!input.trim() || isLoading) return;

    const userMessage = input.trim();
    setInput('');
    setIsLoading(true);

    // Add user message
    const userMsgId = `user-${Date.now()}`;
    setMessages((prev) => [...prev, { id: userMsgId, role: 'user', content: userMessage }]);

    // Add streaming assistant message
    const assistantMsgId = `assistant-${Date.now()}`;
    setMessages((prev) => [
      ...prev,
      { id: assistantMsgId, role: 'assistant', content: '', isStreaming: true },
    ]);

    try {
      let fullContent = '';
      let changes: FoodItemChange[] = [];

      for await (const event of mealAnalysisApi.sendChatMessage(mealId, userMessage)) {
        if (event.text) {
          fullContent += event.text;
          setMessages((prev) =>
            prev.map((msg) =>
              msg.id === assistantMsgId ? { ...msg, content: fullContent } : msg
            )
          );
        }

        if (event.done) {
          changes = event.changes || [];
          setMessages((prev) =>
            prev.map((msg) =>
              msg.id === assistantMsgId
                ? { ...msg, id: event.messageId || assistantMsgId, isStreaming: false, changes }
                : msg
            )
          );
          if (changes.length > 0) {
            setPendingChanges(changes);
          }
        }

        if (event.error) {
          setMessages((prev) =>
            prev.map((msg) =>
              msg.id === assistantMsgId
                ? { ...msg, content: event.error || '„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü', isStreaming: false }
                : msg
            )
          );
        }
      }
    } catch (error) {
      console.error('Chat error:', error);
      setMessages((prev) =>
        prev.map((msg) =>
          msg.id === assistantMsgId
            ? { ...msg, content: '„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü', isStreaming: false }
            : msg
        )
      );
    } finally {
      setIsLoading(false);
    }
  }, [input, isLoading, mealId]);

  const handleApplyChanges = useCallback(async () => {
    if (pendingChanges.length === 0) return;

    try {
      const result = await mealAnalysisApi.applyChatSuggestion(mealId, pendingChanges);
      onUpdate(result.foodItems, result.updatedTotals);
      setPendingChanges([]);
      toast.success('Â§âÊõ¥„ÇíÈÅ©Áî®„Åó„Åæ„Åó„Åü');
    } catch (error) {
      console.error('Apply changes error:', error);
      if (error instanceof TypeError && (error as TypeError).message === 'Failed to fetch') {
        toast.error('„Éç„ÉÉ„Éà„ÉØ„Éº„ÇØ„Å´Êé•Á∂ö„Åß„Åç„Åæ„Åõ„Çì');
      } else {
        toast.error('Â§âÊõ¥„ÅÆÈÅ©Áî®„Å´Â§±Êïó„Åó„Åæ„Åó„Åü');
      }
    }
  }, [mealId, pendingChanges, onUpdate, toast]);

  const handleKeyPress = useCallback(
    (e: React.KeyboardEvent) => {
      if (e.key === 'Enter' && !e.shiftKey) {
        e.preventDefault();
        handleSend();
      }
    },
    [handleSend]
  );

  return (
    <div className="rounded-lg border">
      {/* Messages */}
      <div className="h-64 overflow-y-auto p-4">
        {messages.length === 0 && (
          <div className="text-center text-gray-400">
            <p>AI„Ç¢„Ç∑„Çπ„Çø„É≥„Éà„Å´Ë≥™Âïè„Åó„Å¶„Åè„Å†„Åï„ÅÑ</p>
            <p className="mt-2 text-sm">‰æã: „ÅîÈ£Ø„ÇíÂçäÂàÜ„Å´„Åó„Åü„ÅÑ„ÄÅ„Çø„É≥„Éë„ÇØË≥™„ÇíÂ¢ó„ÇÑ„Åô„Å´„ÅØÔºü</p>
          </div>
        )}
        {messages.map((msg) => (
          <div
            key={msg.id}
            className={`mb-3 ${msg.role === 'user' ? 'text-right' : 'text-left'}`}
          >
            <div
              className={`inline-block max-w-[80%] rounded-lg px-4 py-2 ${
                msg.role === 'user'
                  ? 'bg-blue-500 text-white'
                  : 'bg-gray-100 text-gray-800'
              }`}
            >
              {msg.content}
              {msg.isStreaming && <span className="ml-1 animate-pulse">...</span>}
            </div>
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>

      {/* Pending changes */}
      {pendingChanges.length > 0 && (
        <div className="border-t bg-yellow-50 p-3">
          <p className="mb-2 text-sm font-medium">AI„Åã„Çâ„ÅÆÂ§âÊõ¥ÊèêÊ°à:</p>
          <ul className="mb-2 text-sm text-gray-600">
            {pendingChanges.map((change, i) => (
              <li key={i}>
                {change.action === 'add' && `ËøΩÂä†: ${change.foodItem?.name}`}
                {change.action === 'remove' && 'ÂâäÈô§: (È£üÊùê)'}
                {change.action === 'update' && 'Â§âÊõ¥: (È£üÊùê)'}
              </li>
            ))}
          </ul>
          <div className="flex gap-2">
            <button
              onClick={handleApplyChanges}
              className="rounded bg-green-500 px-4 py-1 text-sm text-white hover:bg-green-600"
            >
              ÈÅ©Áî®„Åô„Çã
            </button>
            <button
              onClick={() => setPendingChanges([])}
              className="rounded px-4 py-1 text-sm text-gray-500 hover:bg-gray-200"
            >
              „Ç≠„É£„É≥„Çª„É´
            </button>
          </div>
        </div>
      )}

      {/* Input */}
      <div className="flex border-t p-3">
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyPress={handleKeyPress}
          placeholder="„É°„ÉÉ„Çª„Éº„Ç∏„ÇíÂÖ•Âäõ..."
          disabled={isLoading}
          className="flex-1 rounded-l-lg border-y border-l px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <button
          onClick={handleSend}
          disabled={isLoading || !input.trim()}
          className="rounded-r-lg bg-blue-500 px-4 py-2 text-white hover:bg-blue-600 disabled:opacity-50"
        >
          ÈÄÅ‰ø°
        </button>
      </div>
    </div>
  );
}
